<template>
  <div style="margin: auto;top:20px;left: 20px;right: 20px;bottom: 20px" v-loading="loadingFlag">
    <div style="margin: 20px">
      <el-radio-group v-model="fileType" size="medium">
        <el-radio :label="1">产品</el-radio>
        <el-radio :label="2">运营</el-radio>
      </el-radio-group>
    </div>

    <el-upload
      drag
      action="test"
      ref="upload"
      style="margin: auto; top:20px;left: 20px;right: 20px;bottom: 20px;width: 380px "
      :auto-upload="false"
      :limit=1
      :on-change="handleFileChange"
      :before-upload="beforeUpload"
      :on-exceed="handleExceed"
      :on-remove="handleRemove"
      :on-success="handleSuccess"
      >
      <i class="el-icon-upload"></i>
      <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
      <div class="el-upload__tip" slot="tip" style="font-size: 14px">注意：需要选择转换类型，且只能上传xmind文件</div>
    </el-upload>

    <div style="margin: 20px">
      <el-button type="text" style="font-size: 15px;" @click="submitUpload()"><i class="el-icon-download"></i>点击下载转换结果文件</el-button>
    </div>

    <a type="text" style="margin-right: 20px;font-size: 15px" href="http://localhost:8080/download?filename=sample1.xmind">产品模板 <i class="el-icon-arrow-right"></i></a>
    <a type="text" style="margin-right: 20px;font-size: 15px" href="http://localhost:8080/download?filename=sample2.xmind">运营模板 <i class="el-icon-arrow-right"></i></a>
  </div>
</template>

<script>
export default {
  name: 'Xmind2xls',
  data () {
    return {
      // 默认选择产品
      fileType: 0,
      // 上传的文件名
      fileName: '',
      // 下载的转换文件链接
      fileLink: '',
      // 加载页面呈现标识
      loadingFlag: false
    }
  },
  methods: {
    // 文件状态改变时的钩子，添加文件、上传成功和上传失败时都会被调用
    handleFileChange (file) {
      if (file.name.split('.')[1] !== 'xmind') {
        this.$message({
          type: 'error',
          message: '上传文件必须为xmind格式'
        })
        this.$refs.upload.clearFiles()
      } else {
        this.fileName = file.name.split('.')[0]
        console.log('上传的文件名：' + this.fileName)
      }
    },
    // 上传的文件被删除时调用
    handleRemove () {
      this.fileName = ''
      console.log('上传的文件名：' + this.fileName)
    },
    // 文件上传成功时调用
    handleSuccess () {
    },
    // 上传文件之前的钩子，参数为上传的文件，若返回 false 或者返回 Promise 且被 reject，则停止上传。
    beforeUpload (file) {
      let formData = new FormData()
      formData.append('file', file)
      formData.append('selection', this.fileType.toString())
      this.handleUpload(formData)
    },
    // 处理文件上传函数
    handleUpload (formData) {
      this.loadingFlag = true
      this.$http.post('http://localhost:8080/upload', formData)
        .then(function (response) {
          this.loadingFlag = false
          console.log(response)
          if (response.body.status === 'S') {
            this.fileLink = response.body.filename
            console.log('转换成excel文件为：' + this.fileLink)
            let aTag = document.createElement('a')
            aTag.href = 'http://localhost:8080/download?filename=' + this.fileLink
            aTag.click()
          } else {
            this.$message({
              type: 'error',
              message: '文件上传失败，请重新上传文件'
            })
          }
        })
        .catch(function (error) {
          this.loadingFlag = false
          console.log(error)
          this.$message({
            type: 'error',
            message: '文件上传失败，请重新上传文件'
          })
        })
    },
    // 文件上传个数超过1的时候提示用户
    handleExceed () {
      this.$message({
        type: 'info',
        message: '只能上传1个文件，如需重新上传请先移除已上传的文件'
      })
    },
    // 上传xmind文件
    submitUpload () {
      console.log('run submit upload file')
      // 如果fileType为0，则是没有选择转换类型
      if (this.fileType === 0) {
        this.$message({
          type: 'error',
          message: '请先选择需转换的案例类型'
        })
        return false
      } else if (this.fileName === '') {
        this.$message({
          type: 'error',
          message: '请先选择需转换的xmind文件'
        })
      } else {
        this.$refs.upload.submit()
      }
    }
  }
}
</script>



